@use "../../styles/index.scss" as *;

.publishing-url-page {
  width: 100%;
  max-width: 100%;
  padding: 0;
  overflow-x: hidden;

  .bottom-dock,
  nav.bottom-dock {
    position: relative !important;
    bottom: auto !important;
    left: auto !important;
    right: auto !important;
    width: 100%;
    z-index: auto !important;
  }

  .bottom-dock--relative {
    position: relative !important;
    bottom: auto !important;
    left: auto !important;
    right: auto !important;
    width: 100%;
    z-index: auto !important;
  }

  &__header {
    margin-bottom: 0;

    h2 {
      margin: 0;
      font-size: 1.8rem;
      font-weight: 700;
      color: var(--color-text);
      text-align: center;
    }
  }

  &__table-container {
    width: 100% !important;
    max-width: 100% !important;
    padding: 0 !important;
    background-color: var(--color-card);
    border: 1px solid var(--color-border);
    overflow-x: auto;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    position: relative;
  }
}

.publishing-url-table {
  width: 100%;
  min-width: 840px; //      
  max-width: 100%; // PC   
  border-collapse: collapse;
  table-layout: fixed; //     

  thead {
    background-color: var(--color-accent);
    color: var(--color-white);

    th {
      padding: px(20) px(16);
      text-align: left;
      font-weight: 600;
      font-size: 0.95rem;
      text-transform: uppercase;
      letter-spacing: 0.5px;
    }
  }

  tbody {
    tr {
      border-bottom: 1px solid var(--color-border);

      &:hover {
        background-color: rgba(12, 124, 89, 0.03);
      }

      &:last-child {
        border-bottom: none;
      }
    }

    td {
      padding: px(20) px(16);
      vertical-align: middle;
      overflow: hidden;
      text-overflow: ellipsis;
      
      //   (1-4)
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4) {
        white-space: nowrap;
        max-width: px(150);
      }
      
      // URL  (5)
      &:nth-child(5) {
        white-space: nowrap;
        max-width: px(300);
        overflow: hidden;
        text-overflow: ellipsis;
      }
      
      //   (6)
      &:nth-child(6) {
        white-space: normal;
        word-break: break-word;
        overflow-wrap: break-word;
        max-width: px(400);
      }
      
      &:first-child {
        font-weight: 600;
        color: var(--color-accent);
        min-width: px(100);
      }

      a {
        color: var(--color-accent);
        text-decoration: none;
        font-weight: 500;
        display: inline-block;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}


//  

//  (768px ~ 1024px)
@media (max-width: 1024px) and (min-width: 769px) {
  .publishing-url-page {
    &__table-container {
      overflow-x: auto;
      -webkit-overflow-scrolling: touch; // iOS  
    }
  }

  .publishing-url-table {
    min-width: 900px; //    
  }
}

@media (max-width: 768px) {
  .publishing-url-page {
    padding: 0; //   

    &__header {
      margin-bottom: 0;

      h2 {
        font-size: 1.5rem;
      }
    }

    &__table-container {
      width: 100%;
      border-radius: 0;
      overflow-x: auto;
      -webkit-overflow-scrolling: touch;
    }
  }

  .publishing-url-table {
    min-width: 900px; //   
    table-layout: fixed; //   

    thead th {
      padding: px(12) px(8);
      font-size: 0.7rem;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      
      //    
      &:nth-child(1) { width: 10%; } // 1
      &:nth-child(2) { width: 10%; } // 2
      &:nth-child(3) { width: 10%; } // 3
      &:nth-child(4) { width: 10%; } // 4
      &:nth-child(5) { width: 30%; } // URL 
      &:nth-child(6) { width: 30%; } // 
    }

    tbody td {
      padding: px(12) px(8);
      font-size: 0.75rem;
      vertical-align: top;
      overflow: hidden;
      
      //     
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4) {
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      
      // URL    
      &:nth-child(5),
      &:nth-child(6) {
        white-space: normal;
        word-break: break-word;
        overflow-wrap: break-word;
      }
      
      a {
        word-break: break-all;
        display: inline-block;
        max-width: 100%;
        overflow-wrap: break-word;
      }
    }
  }
}

// PC (769px )
@media (min-width: 769px) {
  .publishing-url-page {
    &__table-container {
      position: relative;
      max-width: 100%;
    }
  }

  .publishing-url-table {
    min-width: 840px;
    max-width: 100%;
    
    thead {
      position: sticky;
      top: 0;
      z-index: 2;

      th {
        position: sticky;
        top: 0;
        z-index: 3;
        box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    
    tbody {
      td {
        // PC   
        &:nth-child(1) { max-width: px(120); }
        &:nth-child(2) { max-width: px(120); }
        &:nth-child(3) { max-width: px(120); }
        &:nth-child(4) { max-width: px(120); }
        &:nth-child(5) { max-width: px(350); }
        &:nth-child(6) { max-width: px(450); }
      }
    }
  }
}
